.gallery
	.uk-grid
		margin-left: -30px
		> .uk-grid-margin
			margin-top: 30px
		> *
			padding-left: 30px
	.subnav
		margin-bottom: 30px
		ul
			list-style: none
			padding-left: 0
			display: flex
			justify-content: center
			margin: 0
			li
				+ li
					padding-left: 20px
				a
					display: flex
					align-items: center
					justify-content: center
					height: 40px
					background: #fff
					border: 1px solid #d7d7d7
					padding: 0 30px
					text-transform: uppercase
					color: #666666
					transition: 0.3s all
				&.active,&:hover
					a
						background: $mainColor
						border: 1px solid $mainColor
						color: #fff
	.video
		position: relative
		background: #ffffff
		transition: all .3s
		border-radius: 5px
		overflow: hidden
		display: flex
		flex-direction: column
		.title
			font-size: r(18px)
			line-height: 1.3
			font-weight: 600
			flex: 1 1 0%
			padding: 10px 20px 0 20px
			a:hover
				color: $mainColor
		.brief
			line-height: r(22px)
			margin: 10px 20px 20px
			color: #666
			display: -webkit-box
			-webkit-line-clamp: 3
			-webkit-box-orient: vertical 
			overflow: hidden
		.img
			position: relative
			height: 170px
			a
				img
					border-top-left-radius: 5px
					border-top-right-radius: 5px
					width: 100%
					height: 100%
					object-fit: cover
			.overlay
				position: absolute
				width: 100%
				height: 100%
				top: 0 
				left: 0
				content: ''
				z-index: 1
				background: rgba($mainColor,0.6)
				opacity: 0
				pointer-events: none
		.button-play 
			position: absolute 
			top: 50%
			left: 50%
			transform: translate(-50%, -50%)
			z-index: 2
			pointer-events: none
			img 
				max-width: 40px
		&:hover 
			box-shadow: 0 2px 23px 0 rgba(0,0,0,.2)
			.img 
				.overlay 
					opacity: 1


.video-detail
	border-bottom: 1px solid #d7d7d7
	background: #fff
	padding: 40px 0
	.heading
		display: flex
		justify-content: space-between
		align-items: center
		padding-bottom: 15px
		border-bottom: 1px solid #d7d7d7
		time
			display: block
			font-size: r(14px)
			color: #999
			margin-bottom: 10px
	.fullcontent
		padding-top: 30px
		font-size: 16px
		line-height: 24px
		iframe 
			width: 100%
		h4 
			font-weight: bold 
		strong 
			margin-right: 5px

.video-other-list
	padding-top: 30px
	


.social-share 
	a:not(:first-child)
		margin-left: 8px