.recruitment-detail
	.recruitment-detail-content
		background: #fff
		padding: 30px
	.section-title
		text-transform: none
	h3
		text-transform: uppercase
		font-weight: 600
		font-size: r(22px)
		color: $mainColor
		span
			color: #06111b
			text-transform: none
			font-weight: normal
			font-size: r(18px)
	.job-attributes
		table
			width: 100%
			font-size: r(18px)
			tr
				&:first-child
					td
						padding-top: 0
				td
					&:first-child
						font-weight: 600
						padding-left: 0
						width: 30%
	.fullcontent
		font-size: r(18px)
		line-height: r(24px)
		ul
			padding-left: 1rem
			li
				+ li
					margin-top: 10px
	.uk-button-group
		flex-wrap: wrap
		margin-bottom: -10px
		> *
			margin-bottom: 10px
		a
			min-height: 40px
			&.btn-apply
				background: $mainColor
				color: #fff
				text-transform: uppercase
				min-width: 160px
			&.btn-download
				background: #fff
				text-transform: uppercase
				min-width: 230px
				border: 1px solid #d7d7d7
				color: #666666
	.other-jobs
		.title
			padding: 20px 40px
			text-transform: uppercase
			background: $mainColor
			color: #fff
			font-size: r(24px)
			font-weight: 600
			text-align: center
		ul
			list-style: none
			padding-left: 0
			margin: 0
			background: #fff
			li
				a
					display: block
					padding: 15px 30px
					border: 1px solid #d7d7d7
					border-top: 0
					transition: 0.3s all
					.date
						font-size: 14px
						color: #999
						margin-bottom: 10px
						transition: 0.3s all
					.name
						font-size: 18px
					&:hover
						border-left: 5px solid $mainColor
						color: $mainColor
						.date
							color: $mainColor