.product-category-section
	margin-top: 50px
	&:last-child
		margin-bottom: 50px
	.category-title
		display: flex
		justify-content: space-between
		align-items: flex-end
		padding-bottom: 10px
		border-bottom: 1px solid #e5e5e5
		margin-bottom: 30px
		.section-title
			margin-bottom: 0
		.view-more
			color: $mainColor
			font-size: 14px
			font-weight: 600
	.product-list
		.uk-grid
			margin-left: -1px
			margin-bottom: -1px
			> *
				padding-left: 1px
				margin-bottom: 1px
		.category-tab-wrap
			list-style: none
			padding-left: 0
			margin: 0
			display: flex
			border-bottom: 2px solid $mainColor
			background: #ffffff
			li
				a
					min-height: 38px
					display: flex
					align-items: center
					padding: 0 30px
					font-size: r(15px)
					color: $gray-1
					position: relative
					&:before
						content: ''
						position: absolute
						width: 0
						height: 0
						border-style: solid
						border-width: 0 4.5px 5px 4.5px
						border-color: transparent transparent #d8070e transparent
						bottom: 0
						left: 50%
						transform: translateX(-50%)
						opacity: 0
						transition: 0.3s all
				&:hover,&.uk-active
					a
						color: $mainColor
						&:before
							opacity: 1