.product-list
	.product
		background: #fff
		padding: 12px
		display: flex
		overflow: hidden
		&.product-col
			position: relative
			flex-direction: column
			.img
				height: r(236px)
				width: 100%
				position: relative
				img
					position: absolute
					top: 50%
					left: 50%
					transform: translate(-50%, -50%)
					max-width: 235 / 255 * 100%
					max-height: 203px
			.caption
				display: flex
				flex-direction: column
				flex: 1 1 0%
			.title
				flex: 1 1 0%
				margin-top: 10px
		&.product-row
			.img
				width: 167px
				position: relative
				overflow: hidden
			.caption
				padding-left: 20px
				flex: 1
			.title
				margin-bottom: 25px
			.purchase-wrapper
				margin-bottom: 10px
			.purchase-button
				top: 100%
				height: 100%
				padding: 0 15px
				.input-amount
					input
						width: 60px
				.btn-addtocart
					width: r(140px)
		&.show
			.purchase-button
				top: 0
		.purchase-button
			position: absolute
			top: -100%
			left: 0
			width: 100%
			height: r(250px)
			background: rgba(#000000, 0.3)
			display: flex
			align-items: center
			justify-content: center
			flex-direction: column
			z-index: 10
			transition: 0.3s all
			.input-amount
				display: flex
				border-radius: 25px
				overflow: hidden
				.btn-dec,.btn-inc
					width: r(40px)
					height: r(40px)
					display: flex
					align-items: center
					justify-content: center
					background: #fff
					font-size: 24px
				input
					height: r(40px)
					width: r(80px)
					border: 0
					text-align: center
			.btn-addtocart
				display: flex
				align-items: center
				justify-content: center
				height: r(40px)
				width: r(160px)
				border-radius: 25px
				background: $mainColor
				font-weight: 700
				margin-top: 10px
				text-transform: uppercase
				font-size: r(14px)
				color: #fff
		.title
			flex: 1 1 0%
			margin-bottom: 15px
			line-height: r(20px)
		.purchase-wrapper
			flex: 1 1 0%
			margin-top: auto
			display: flex
			justify-content: space-between
			align-items: flex-start
		.price
			font-size: r(18px)
			line-height: r(22px)
		.oldprice
			del
				font-size: r(14px)
				color: $gray-1
				line-height: r(22px)
			strong
				font-size: r(15px)
				margin-left: 5px
				&.red
					color: $mainColor
				&.green
					color: green
		.buy-button
			font-size: 18px
			display: flex
			margin-top: -6px
			a
				display: flex
				align-items: center
				justify-content: center
				width: 30px
				height: 30px
				border-radius: 50px
				span
					font-size: 18px
				.amount
					font-size: 14px
					margin-left: 5px
			.add-wishlist
				color: $gray-1
				margin-right: 10px
				&.active
					.mdi
						color: $mainColor
						&:before
							content: '\f2d1'
			.toggle-buy
				color: $mainColor
				&:hover
					background: $mainColor
					color: white
		.flash-sale
			display: flex
			align-items: center
			justify-content: space-between
			margin-top: 10px
			flex: 1 1 0%
			.flash-progress
				height: 16px
				overflow: hidden
				background-color: #ffcecf
				border-radius: 8px
				position: relative
				width: 115px
				.flash-progress-bar
					float: left
					width: 0%
					height: 100%
					font-size: 10px
					line-height: 16px
					color: #fff
					text-align: center
					background-color: $mainColor
					transition: width .6s ease
					span
						position: absolute
						top: 50%
						left: 50%
						transform: translate(-50%, -50%)
			.flash-countdown
				font-size: 12px
				text-align: right
	@media screen and ( max-width: 768px )
		.product
			.flash-sale
				flex-direction: column-reverse
				align-items: flex-start
				justify-content: flex-start
				.flash-countdown
					margin-bottom: 5px
