.banner-section-wrapper
	.uk-container
		position: relative

.breadcrumb-wrapper
	background: rgba(#000 , 0.5)
	position: absolute
	bottom: 0
	left: 15px
	right: 15px
	color: #fff
	padding: 0 15px
	.breadcrumb
		list-style: none
		padding-left: 0
		margin: 0
		display: flex
		font-size: r(14px)
		align-items: center
		li
			+ li
				margin-left: 10px
				display: flex
				align-items: center
				&:before
					content: '|'
					margin-right: 10px
			a
				display: flex
				align-items: center
				min-height: 40px
			&:first-child
				a
					font-size: 0
					&:before
						font-family: "Material Design Icons"
						content: '\f2dc'
						font-size: r(18px)