header
	.top-header-wrapper
		padding: 5px 0
		background: #fff
	.top-header
		align-items: center
	.search-wrapper
		margin-left: 30px
		width: 420 / 1230 * 100%
		.uk-search
			display: flex
			width: 100%
			input
				width: 370 / 420 * 100%
				border-radius: 5px 0px 0px 5px
				font-size: 15px
				padding: 0 15px
			button
				display: flex
				justify-content: center
				background: $mainColor
				color: white
				width: 50px
				border: 0
				border-radius: 0px 5px 5px 0px
				font-size: 24px
				cursor: pointer
	.top-header-item-list
		display: flex
	.top-header-item
		margin-left: 25px
		display: flex
		align-items: center
		.top-header-link
			display: flex
			align-items: center
			color: $gray-1
			.lnr
				font-size: 28px
			.text
				font-size: 14px
				margin-left: 10px
				line-height: 18px
				white-space: nowrap
	.language-wrapper,
		margin-left: 25px
	.language
		position: relative
		.current 
			display: flex
			align-items: center
			justify-content: center
			width: 60px
			height: 45px
			border: 1px solid $gray-3
			border-radius: 5px
			font-size: 14px
			color: #989898
			.lnr
				margin-left: 5px
		.uk-dropdown
			min-width: auto
			width: 100%
			padding: 0
			border-radius: 5px
			ul
				li
					a
						display: flex
						align-items: center
						padding: 10px
	.cart-wrapper
		margin-left: 25px
		width: 160 / 1230 * 100%
	.cart
		.cart-toggle 
			display: flex
			align-items: center
			padding: 0 10px
			height: 45px
			border: 1px solid $mainColor
			border-radius: 5px
			.icon
				margin-right: 15px
				font-size: r(24px)
				color: $mainColor
			.cart-text
				color: $gray-1
				.amount
					color: $mainColor
					font-weight: 700
	.bottom-header-wrapper
		background: #ebebeb
	.product-category-wrapper
		width: 220 / 1230 * 100%
		position: relative
		.toggle
			display: flex
			align-items: center
			justify-content: center
			min-height: 40px
			background: $mainColor
			color: white
			text-transform: uppercase
			font-weight: 700
		.uk-dropdown
			width: 100%
			min-width: auto
			padding: 0
		.uk-nav
			li
				a
					display: flex
					align-items: center
					min-height: 50px
					padding: 0 15px
					.icon
						img
							filter: grayscale(100%)
					.text
						margin-left: 10px
				&.active,&:hover
					a
						color: $mainColor
						.icon
							img
								filter: none
				+ li
					border-top: 1px solid #ebebeb
	.menu-wrapper
		width: 960 / 1230 * 100%
	.menu
		.uk-navbar-nav
			justify-content: center
			> li
				position: relative
				> a
					min-height: 40px
					padding: 0 20px
					font-weight: 700
					font-size: 1rem
					color: #333
				.uk-dropdown
					width: 100%
					min-width: auto
					padding: 0
				.uk-nav
					li
						a
							display: flex
							align-items: center
							min-height: 50px
							padding: 0 15px
						&.active,&:hover
							a
								color: $mainColor
						+ li
							border-top: 1px solid #ebebeb
				&.active
					> a
						color: $mainColor
	.dropdown-menu-wrapper
		flex: 1 1 0%
		.toggle
			display: flex
			align-items: center
			justify-content: center
			min-height: 40px
			background: #2f2e2c
			font-size: 36px
			color: #fff
		.uk-dropdown
			padding: 20px 30px
		.uk-nav
			li
				a
					padding: 10px 0
				&.active
					a
						color: $mainColor
	@media screen and ( max-width: 1280px )
		.logo
			img
				max-width: 60px
	@media screen and ( max-width: 1200px )
		.top-header
			justify-content: space-between
		.top-header-item
			.top-header-link
				flex-direction: column
				align-items: center
				.lnr
					font-size: 20px
				.text
					font-size: 12px
					text-align: center
					margin-left: 0
		.menu
			.uk-navbar-nav
				> li
					> a
						font-size: 13px
		.product-category-wrapper
			.toggle
				font-size: 14px
	@media screen and ( max-width: 1025px )
		.top-header-item-list
			position: fixed
			bottom: 0
			left: 0
			width: 100%
			background: #ebebeb
			padding: 10px 0
			z-index: 500
		.top-header-item
			width: 25%
			margin-left: 0
			padding: 0 5px
			justify-content: center
		.dropdown-menu-wrapper
			width: 60px
			flex: none
			margin-left: auto
		.menu-wrapper
			width: auto
		.menu
			.uk-navbar-nav
				flex-direction: column
				> li
					> a
						padding: 10px 0
						justify-content: flex-start
						font-weight: 400
						text-transform: none
						min-height: auto
		.top-header
			justify-content: flex-start
		.cart-wrapper
			width: 136px
			margin-left: auto
			.cart
				.cart-toggle
					height: 40px
		.product-category-wrapper
			width: 220px
		.search-wrapper
			width: 64.14634%
			padding: 0 25px
			margin-left: 0
	@media screen and ( max-width: 576px )
		.search-wrapper
			width: 75%
			padding: 0 10px
		.cart-wrapper
			width: 60px
			.cart
				.cart-toggle
					.icon
						margin-right: 5px
					.cart-text
						.text
							display: none
		.bottom-header-wrapper
			.uk-container
				padding: 0
