.recruitment-1
	.section-title
		margin-bottom: 30px
	.item
		background: #fff
		box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.05)
		+ .item
			margin-top: 30px
		&:nth-of-type(2n)
			flex-direction: row-reverse
		&.item-1
			.img
				+img-scale(400 / 705 * 100%)
		&.item-2
			.img
				+img-scale(620 / 705 * 100%)
		.content-wrapper
			padding: 40px
		.title
			font-weight: 600
			text-transform: uppercase
			font-size: r(24px)
			color: $mainColor
			margin-bottom: 20px
		.content
			font-size: r(18px)
			line-height: r(24px)
			strong
				font-weight: 600
	@media screen and ( max-width: 576px )
		.item
			.content-wrapper
				padding: 20px