.home-1
	.wrapper-1
		width: 220 / 1230 * 100%
	.wrapper-2
		width: 749 / 1230 * 100%
	.wrapper-3
		width:  (1230 - 749 - 220 )/ 1230 * 100%
		padding-left: 10px
	.ModuleContent
		> *
			height: 100%
	.product-category
		background: #fff
		width: 100%
		.uk-nav
			li
				a
					display: flex
					align-items: center
					min-height: 50px
					padding: 0 15px
					.icon
						img
							filter: grayscale(100%)
					.text
						margin-left: 10px
				&.active,&:hover
					a
						color: $mainColor
						.icon
							img
								filter: none
				+ li
					border-top: 1px solid #ebebeb
		
	.banner-home
		height: 100%
		display: flex
		.uk-slider-items
			height: 100%
		.slider-nav
			background: #fff
			font-size: 18px
			display: flex
			align-items: center
			justify-content: center
			width: 30px
			height: 30px
			&.uk-position-center-left
				left: 0
			&.uk-position-center-right
				right: 0
		.banner
			display: block
			height: 100%
			img
				height: 100%
				width: 100%
				object-fit: cover
	.feedback-wrapper
		background: #fff
		padding: 20px
		.uk-slider-container
			display: flex
			flex-direction: column
			justify-content: space-between
			height: 100%
		.slider-navs
			display: flex
			justify-content: center
			align-items: center
			margin-top: 20px
			padding-top: 10px
			border-top: 1px solid #ebebeb
			a
				color: #989898
				font-size: 18px
				+ a
					margin-left: 5px
					display: flex
					align-items: center
					&::before
						content: "|"
						margin-right: 5px
		.item
			.img
				display: flex
				width: 100px
				height: 100px
				align-items: center
				justify-content: center
				border-radius: 50%
				border: 2px solid #e7f6fd
				margin: 0 auto
				overflow: hidden
			.content
				margin-top: 20px
				.fas
					font-size: r(24px)
					color: #d6d6d6
				.text
					font-size: r(14px)
					line-height: r(22px)
					color: #999999
					margin: 5px 0
			a
				display: inline-block 
				text-align: center
				font-size: r(14px)
				color: #339fd7
				border-bottom: 1px solid #339fd7
				margin-bottom: 10px
			.title
				font-size: r(14px)
				font-weight: 700
				line-height: r(20px)
	@media screen and ( max-width: 1025px )
		.wrapper-1
			display: none
		.wrapper-2
			width: 100%
		.wrapper-3
			width: 100%
			padding-left: 0
	@media screen and ( max-width: 576px )
		.uk-container
			padding: 0