.support-detail
	.section-title
		padding-bottom: 15px
		border-bottom: 1px solid $mainColor
		margin-bottom: 30px
	.list-item
		> :nth-child(n+2)
			margin-top: 10px
		li
			background: #fff
			.uk-accordion-title 
				display: flex
				align-items: center
				justify-content: space-between
				padding: 15px 30px
				font-size: 1rem
				&:before
					display: none
			.uk-accordion-content
				padding: 0 30px 30px
				font-weight: 300
				line-height: 1.5
			&.uk-open
				.uk-accordion-title 
					color: $mainColor
					.mdi-chevron-down
						display: block
						transform: rotate(180deg)
		.rating-box 
			display: flex
			align-items: center
		.rating
			margin-right: 20px
			.list-item-rating 
				display: flex
				em 
					color: #d8070d
					font-size: 11px
					margin-right: 5px
	@media (max-width: 768px)
		.list-item
			li
				.uk-accordion-content
					padding: 0 30px 15px