.about-3
	background: #fff
	.item
		align-items: center
		&:nth-of-type(odd)
			flex-direction: row-reverse
			.content
				padding-right: 30px
		&:nth-of-type(even)
			.content
				padding-left: 30px
		.title
			font-size: r(18px)
			line-height: r(24px)
			display: inline-block
			margin-bottom: 20px
			border-bottom: 2px solid $red-1
			font-weight: 700
			padding-bottom: 5px
		.text
			font-size: r(18px)
			line-height: r(24px)
			color: #999999
	@media screen and ( max-width: 1025px )
		.item
			.img
				margin-bottom: 30px
				img
					width: 100%
			.content
				padding: 0 !important