.about-2
	background-image: url('../img/about/bg.jpg')
	background-repeat: no-repeat
	.year-dot-slider
		margin-top: 40px
		position: relative
		&::after
			content: ''
			position: absolute
			height: 2px
			width: 100%
			background: #fad3d4
			bottom: 4px
		.year
			cursor: pointer
			.text
				text-align: center
				font-size: 18px
				color: #999999
				font-weight: 700
			.dot
				width: 10px
				height: 10px
				border-radius: 50%
				background: #fad3d4
				margin: 20px auto 0
		.swiper-slide-thumb-active
			.year
				.text
					color: #e52429
				.dot
					background: #e52429
		.swiper-nav
			.swiper-button
				position: absolute
				bottom: 2px
				font-size: 30px
				color: #e52429
				font-weight: 300
				cursor: pointer
				z-index: 20
				span
					line-height: 0
					display: block
				&.swiper-prev
					left: -30px
				&.swiper-next
					right: -30px
	.year-content-slider
		margin-top: 40px
		position: relative
		&:before
			position: absolute
			content: ''
			width: r(105px)
			height: r(105px)
			border-left: 2px solid #e52429
			border-top: 2px solid #e52429
			left: -11px
			top: -11px
			background: #fff
		.item
			.img
				+img-scale(373 / 590 * 100%)
	.text
		.section-title
			margin-bottom: r(30px)
		.content
			font-size: r(18px)
			line-height: r(22px)
	@media screen and ( max-width: 1025px )
		.year-dot-slider
			.swiper-nav
				.swiper-button
					&.swiper-prev
						left: -10px
					&.swiper-next
						right: -10px