.product-list-section
	.heading
		display: flex
		justify-content: space-between
		align-items: center
		margin-bottom: 30px
		padding-bottom: 10px
		border-bottom: 1px solid #e5e5e5
		.section-title
			margin-bottom: 0
		.sort
			padding-left: 10px
			position: relative
			.current
				display: flex
				align-items: center
				justify-content: space-between
				height: 30px
				padding: 0 15px
				min-width: 200px
				border: 1px solid #d7d7d7
				font-size: 14px
				font-weight: 300
			.uk-dropdown
				padding: 0
				ul
					padding-left: 0
					margin: 0
					list-style: none
					li
						+ li
							border-top: 1px solid #d7d7d7
						a
							display: flex
							align-items: center
							min-height: r(36px)
							padding: 0 15px
							transition: all 0.3s
							&:hover
								color: $mainColor
								box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.1)
	.category-tab-wrap 
		list-style: none
		padding-left: 0
		margin: 0
		display: flex
		border-bottom: 2px solid $mainColor
		background: #ffffff
		li
			a
				min-height: 38px
				display: flex
				align-items: center
				padding: 0 30px
				font-size: r(15px)
				color: $gray-1
				position: relative
				&:before
					content: ''
					position: absolute
					width: 0
					height: 0
					border-style: solid
					border-width: 0 4.5px 5px 4.5px
					border-color: transparent transparent #d8070e transparent
					bottom: 0
					left: 50%
					transform: translateX(-50%)
					opacity: 0
					transition: 0.3s all
			&:hover,&.uk-active
				a
					color: $mainColor
					&:before
						opacity: 1
	.product-list
		.uk-grid
			margin-left: -1px
			margin-bottom: -1px
			> *
				padding-left: 1px
				margin-bottom: 1px
