
.badge 
	background-color: #6394F8
	border-radius: 10px
	color: white
	display: inline-block
	font-size: 12px
	line-height: 1
	padding: 3px 7px
	text-align: center
	vertical-align: middle
	white-space: nowrap


.shopping-cart 
	float: right
	background: white
	
	position: relative
	border-radius: 3px
	@media screen and ( min-width: 768px )
		width: 320px

.shopping-cart-header 
	border-bottom: 1px solid #E8E8E8
	padding-bottom: 15px
	.shopping-cart-total 
		float: right

.shopping-cart-items 
	list-style: none
	padding: 0
	margin: 0
	padding-top: 20px
	max-height: 400px
	overflow-y: auto
	padding-right: 8px
	margin-bottom: -18px
	li 
		margin-bottom: 18px
		display: flex
		line-height: 1.5
		border-bottom: 1px dashed #ccc
		padding-bottom: 15px
	.img
		width: 80px
		a
			display: block
			padding-top: 100%
			position: relative
			img
				position: absolute
				top: 50%
				left: 50%
				transform: translate(-50%, -50%)
				max-height: 80px
	.info
		width: calc(100% - 80px)
		padding-left: 20px
		.item-name 
			display: block
			font-size: 16px
			color: $mainColor
			font-weight: 500
		.item-price 
			display: block
			color: $mainColor
			margin-right: 8px
			font-size: 14px
		.item-quantity 
			display: block
			color: #ABB0BE
			font-size: 14px
	&::-webkit-scrollbar
		width: 5px
		background: #d7d7d7
	&::-webkit-scrollbar-thumb
		background: $mainColor

.cart-icon 
	color: #515783
	font-size: 24px
	margin-right: 7px
	float: left


.shopping-cart 
	.button 
		background: $mainColor
		color: white
		text-align: center
		padding: 12px
		text-decoration: none
		display: block
		border-radius: 3px
		font-size: 16px
		margin: 25px 0 15px 0
		&:hover 
			background: lighten($mainColor, 3%)
	


.clearfix:after 
	content: ""
	display: table
	clear: both

