.section-title {
	font-size: r(30px);
	font-weight: 700;
	&.section-title-red {
		color: $mainColor;
		span {
			font-weight: 400; } } }

.slider-nav {
	font-size: r(30px);
	padding: 0;
	@media screen and ( min-width: 1025px ) {
		&.uk-position-center-left {
			left: -45px; }
		&.uk-position-center-right {
			right: -45px; } } }

.maps_popup {
	padding: 20px;
	h1 {
		font-size: 16px;
		color: $mainColor;
		line-height: 20px;
		font-weight: 600;
		margin-bottom: 10px; }
	p {
		margin: 0;
		font-size: 14px;
		font-weight: 300;
		line-height: 24px;
		color: #666666; }
	a {
		font-weight: 300;
		color: $mainColor;
		border-bottom: 1px solid $mainColor;
		font-size: 14px; } }

