.product-detail-wrapper
	border-radius: 5px
	background: #fff
	.uk-grid
		margin-left: -24px
		> *
			padding-left: 24px
	.product-title
		padding: 30px 30px 0 30px
		.section-title
			border-bottom: 1px solid #e5e5e5
			padding-bottom: 10px
			margin-bottom: 0
	.product-detail
		padding: 30px
	.product-image
		.swiper-slide
			padding-top: 100%
				position: relative
				overflow: hidden
			.img
				position: absolute
				top: 50%
				left: 50%
				transform: translate(-50%,-50%)
				width: 100%
				height: auto
				transition: .2s ease-in-out
				img
					width: 100%
					height: 100%
					object-fit: cover
			
	.product-thumbnail
		padding: 0 20px 
		position: relative
		margin-top: 30px
		.img
			padding-top: 100%
			position: relative
			border: 1px solid #e5e5e5
			img
				position: absolute
				top: 50%
				left: 50%
				transform: translate(-50%,-50%)
				max-width: 90%
		.swiper-slide-thumb-active
			.img
				border: 1px solid $mainColor
		.swiper-nav
			position: absolute
			top: 50%
			transform: translateY(-50%)
			z-index: 25
			font-size: 16px
			&.swiper-prev
				left: 0
			&.swiper-next
				right: 0
	.product-content
		.price-wrapper
			padding-bottom: 10px
			border-bottom: 1px solid #e5e5e5
			margin-bottom: 20px
		.price
			font-size: r(30px)
			color: $mainColor
		.oldprice
			font-size: r(15px)
			color: $gray-2
		.discount
			vertical-align: 100%
			margin-left: 10px
			display: inline-flex
			width: 50px
			height: 20px
			border-radius: 25px
			background: #333
			color: white
			font-size: 12px
			align-items: center
			justify-content: center
	.product-info
		padding-bottom: 10px
		border-bottom: 1px solid #e5e5e5
		margin-bottom: 20px
		table
			border-collapse: collapse
			width: 100%
			tr
				td
					padding: 10px 0
					&:first-child
						color: $gray-2
						width: 40%
				&:first-child
					td
						padding-top: 0
	.purchase-button-detail
		display: flex
		align-items: center
		padding-bottom: 20px
		border-bottom: 1px solid #e5e5e5
		margin-bottom: 20px
		flex-wrap: wrap
		.button-top
			width: 100%
			display: flex 
			align-items: center
			margin-bottom: 15px
			span
				margin-left: 10px
		.input-amount
			display: flex
			overflow: hidden
			border-radius: 25px
			.btn-dec,.btn-inc
				width: r(40px)
				height: r(40px)
				display: flex
				align-items: center
				justify-content: center
				background: #d7d7d7
				font-size: 24px
			input
				height: r(40px)
				width: r(80px)
				border: 0
				text-align: center
				background: #ebebeb
		.btn-addtocart
			display: flex
			align-items: center
			justify-content: center
			height: r(40px)
			width: r(160px)
			border-radius: 25px
			background: $mainColor
			font-weight: 700
			margin-left: 10px
			text-transform: uppercase
			font-size: r(14px)
			color: #fff
	.btn-add-cart
		display: flex
		align-items: center
		justify-content: center
		height: r(40px)
		width: r(160px)
		border-radius: 25px
		background: $mainColor
		font-weight: 700
		width: 100%
		text-transform: uppercase
		font-size: r(14px)
		color: #fff
	.product-share
		display: flex 
		align-items: center
		span
			color: $gray-1
			font-weight: 600
		a
			margin-left: 10px
			width: 30px
			height: 30px 
			display: inline-flex 
			justify-content: center 
			align-items: center 
			border-radius: 50%
			overflow: hidden
			img 
				object-fit: cover
				width: 100%
				height: 100%
	.shop-policy
		margin-top: 30px
		padding: 0 15px
		box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.1)
		border-radius: 5px
		table
			tr
				+ tr
					border-top: 1px solid #e5e5e5
				td
					padding: 15px 0
					&:first-child
						width: 40px
					&:last-child
						padding: 0 15px
						font-size: 15px
	.product-more-info,.product-related
		position: relative
		> .title
			padding-bottom: 13px
			border-bottom: 1px solid #f2f2f2
			color: $mainColor
			font-weight: 600
			span
				display: inline-block
				position: relative
				&:before
					position: absolute
					content: ''
					width: 100%
					bottom: -14px
					left: 0
					height: 1px
					background: $mainColor
	.product-related
		.product-slider
			box-shadow: none
		> .title
			font-size: r(24px)
			color: #333
			margin-bottom: 30px
	.product-more-info
		.content
			padding-top: 30px
			line-height: r(22px)
			position: relative
			max-height: 300px
			overflow: hidden
			&:before
				content: ''
				background: linear-gradient(rgba(255, 255, 255, 0.8), rgb(255, 255, 255))
				bottom: 0px
				height: 100px
				left: 0px
				position: absolute
				width: 100%
			&.show
				max-height: none
				&:before
					display: none
		.view-more
			position: absolute
			left: 50%
			transform: translateX(-50%)
			bottom: 0
			.lnr
				font-size: 14px
				margin-left: 5px
				display: inline-block
			&.view-less
				font-size: 0
				&:before
					content: 'Rút gọn'
					font-size: 1rem
				.lnr
					transform: rotate(180deg)
	.product-viewed
		> .title
			display: inline-block
			font-size: r(24px)
			font-weight: 600
			padding-bottom: 5px
			border-bottom: 1px solid $mainColor
		.wrapper 
			.caption
				.title
				
				.price-wrapper
					.price
						font-size: 0.875rem
					.oldprice
						del
							font-size: 0.75rem 
						>strong 
							font-size: 0.8125rem
		.product
			&.product-row
				align-items: center
				.img
					width: 80px
				.title
					margin-bottom: 0
					margin-left: 6px
	.product-related
		.product-slider 
			.slider-nav
				&.uk-position-center-left
					left: -20px
				&.uk-position-center-right
					right: -20px
	@media screen and ( max-width: 576px )
		.product-detail
			padding: 0 10px
		.product-title
			padding: 30px 15px 0
		.product-related
			.product-list
				.product
					.purchase-wrapper
						flex-direction: column
