.home-product-category
	margin-top: 50px
	.wrapper
		background: #fff
	.category-title
		a
			display: flex
			align-items: center
			justify-content: space-between
			min-height: 40px
			background: $mainColor
			color: white
			padding: 10px 20px 10px 30px
			font-size: r(18px)
			text-transform: uppercase
			line-height: 1.3
	.category-img
		flex: 1 1 0%
		.img
			+img-scale(465 / 376 * 100% )
	.category-tab
		display: flex
		flex-direction: column
		.product-wrapper
			&:not(.uk-first-column)
				border-left: 1px solid #ebebeb
			&.uk-grid-margin
				border-top: 1px solid #ebebeb
		ul
			list-style: none
			padding-left: 0
			margin: 0
			display: flex
			border-bottom: 2px solid $mainColor
			li
				a
					min-height: 38px
					display: flex
					align-items: center
					padding: 0 30px
					font-size: r(15px)
					color: $gray-1
					position: relative
					&:before
						content: ''
						position: absolute
						width: 0
						height: 0
						border-style: solid
						border-width: 0 4.5px 5px 4.5px
						border-color: transparent transparent #d8070e transparent
						bottom: 0
						left: 50%
						transform: translateX(-50%)
						opacity: 0
						transition: 0.3s all
				&:hover,&.uk-active
					a
						color: $mainColor
						&:before
							opacity: 1
		.product-tab-container
			flex: 1 1 0%
			.tab-content
				height: 100%
				.product-list
					height: 100%
					display: flex
	@media screen and ( max-width: 576px )
		.category-img
			display: none
		.category-tab
			ul
				li
					a
						padding: 0 10px