.news-list-section
	.section-title
		margin-bottom: 30px

.news-list
	margin-bottom: 50px
	.news
		display: flex
		flex-direction: column
		background: #fff
		border-radius: 5px
		overflow: hidden
		transition: 0.3s all
		.date
			display: inline-flex
			font-size: r(14px)
			color: #666
			margin-bottom: 10px
			padding: 20px 20px 0 20px
			.lnr
				margin-right: 10px
		.title
			font-size: r(18px)
			line-height: 1.3
			font-weight: 600
			flex: 1 1 0%
			padding: 0 20px 0 20px
			a:hover
				color: $mainColor
		.brief
			line-height: r(22px)
			margin: 15px 20px 20px
			color: #666
			display: -webkit-box
			-webkit-line-clamp: 3
			-webkit-box-orient: vertical 
			overflow: hidden
		.img
			a
				+img-scale(265 / 424 * 100%)
		&:hover
			box-shadow: 0px 2px 23px 0px rgba(0, 0, 0, 0.2)