.contact
	.contact-info-wrapper
		background: #fff
		border: 1px solid #d7d7d7
		padding: 50px
		display: flex
		border-radius: 5px
	.contact-info
		text-align: center
		
		ul
			padding-left: 0
			margin-bottom: 0
			list-style: none
			text-align: left
			li
				display: flex
				font-size: r(18px)
				line-height: r(26px)
				+ li
					margin-top: 10px
				.lnr
					margin-right: 8px
					font-size: 1rem
					color: $mainColor
		.social
			margin-top: 15px
			text-align: left
			a
				+ a
					margin-left: 8px
	.contact-form
		.wrap-form
			position: relative
			.form-group
				+ .form-group
					margin-top: 20px
				input[type="text"],textarea
					height: 50px
					background: #fff
					padding: 0 20px
					border: 1px solid #d7d7d7
					width: 100%
					font-size: 16px
					border-radius: 5px
				textarea
					height: 170px
					padding: 5px 20px
		.frm-btnwrap
			display: flex
			margin-top: 30px
			@media (max-width: 767px)
				flex-wrap: wrap 
				.message 
					padding-left: 0!important
					padding-top: 15px
			.frm-btn
				input[type="submit"]
					height: 50px
					width: 110px
					background: $mainColor
					text-transform: uppercase
					color: #ffffff
					border: 0
					letter-spacing: 0.1rem
					font-size: 16px
					cursor: pointer
					border-radius: 5px
		.message 
			font-size: 16px 
			line-height: 23px
			font-style: italic
		.upload-main 
			display: flex 
			align-items: center
			padding-bottom: 20px
			label 
				font-weight: bold
		.uploadfile 
			display: flex
			align-items: center
			position: relative
			
			.upload-wrapper 
				display: flex
				align-items: center
				justify-content: flex-start
				cursor: pointer
				background-color: #ebebeb
				padding: 10px 30px
				border-radius: 5px
				overflow: hidden
				color: #999
				margin-left: 30px
				&.success
					.file-success-text
						opacity: 1
						visibility: visible
						transform: none
						color: green
						padding-left: 30px
						font-size: 14px
				input[type="file"]
					width: 100%
					position: absolute
					left: 0
					right: 0
					opacity: 0
					top: 0
					bottom: 0
					cursor: pointer
					z-index: 1
				.file-upload-text 
					opacity: 1
					visibility: visible
					transition: 0.2s linear all
				.file-success-text 
					opacity: 0
					transition: 0.2s linear all
					visibility: hidden
					transform: translateX(200px)
					position: absolute
					left: 0 
					top: calc(100% + 7px)
					display: flex 
					align-items: center
					#file-upload-name
						margin: 0 0 0 20px
						font-size: 12px
						color: #999999
			.note 
				margin-left: 15px 
				color: #999
.contact-map
	background-image: url(../img/map-bg.jpg)
	background-repeat: no-repeat
	background-size: cover
	iframe
		border-radius: 5px
		width: 100%
		height: 790px
	@media screen and ( max-width: 768px )
		iframe
			height: 395px 