.dealer
	.section-title
		margin-bottom: 30px
	.filter-map
		background: #333
		padding: 30px
		.form-group
			+ .form-group
				margin-top: 20px
			label
				font-weight: 700
				display: block
				margin-bottom: 8px
				color: #fff
			select
				border: 1px solid #f2f2f2
				font-size: r(14px)
	#list,.list
		padding-left: 0
		margin-bottom: 0
		list-style: none
		box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.1)
		max-height: 550px
		overflow-y: auto
		background: #fff
		margin-top: 30px
		padding-right: 8px
		> li
			padding: 30px
			border-bottom: 1px solid rgba(#999999, 0.5)
			cursor: pointer
			p
				margin: 0
				line-height: r(24px)
				color: #666666
				display: flex
				+ p
					margin-top: 5px
				&.email
					display: none
				.lnr
					margin-right: 8px
				&.name
					color: $mainColor
					line-height: r(20px)
					text-transform: uppercase
					font-weight: 600
			&.active
				background: $mainColor
				*
					color: #fff !important
		&::-webkit-scrollbar
			width: 4px
			background: #cccccc
		&::-webkit-scrollbar-thumb
			background: $mainColor
	#map,#map_canvas,.map,.map_canvas
		height: 100%
		img,iframe
			width: 100%
			height: 100%
		.map-inter
			height: 100%
			width: 100%
	@media screen and ( max-width: 1025px )
		#map,#map_canvas,.map,.map_canvas
			height: 500px
.signup-wrapper 
	padding: 70px 0
	display: flex 
	justify-content: center
	background: #ffffff
	.form 
		display: flex 
		flex-wrap: wrap
		margin-left: -15px
		margin-right: -15px
		.form-group 
			flex: 0 0 50%
			max-width: 50% 
			padding-left: 15px
			padding-right: 15px
			margin-bottom: 15px
			@media (max-width: 767px)
				flex: 0 0 100%
				max-width: 100%
			input[type="text"]
				width: 100%
				border: none
				border-radius: 5px
				background: #f0f0f0
				padding: 10px 20px
				width: 100% !important
				font-size: 18px
				outline: none
			label 
				padding-left: 20px
				font-size: 20px
				margin-bottom: 5px
				text-transform: capitalize
				display: block
			.btn-wrap 
				display: flex
				align-items: flex-start
				@media (max-width: 767px)
					flex-wrap: wrap 
					.message 
						padding-left: 0!important
						padding-top: 15px
				input[type="submit"]
					cursor: pointer
					border: 0
					background: #d8070d
					padding: 10px 30px
					font-weight: 500
					color: #fff
					font-size: 16px
					letter-spacing: .1rem
					text-transform: uppercase
					border-radius: 5px
			.message 
				padding-left: 50px
				font-size: 16px 
				line-height: 23px
				font-style: italic
		.textarea,.btn
			flex: 0 0 100%
			max-width: 100%
			textarea 
				width: 100%
				width: 100%
				border: none
				border-radius: 5px
				background: #f0f0f0
				padding: 10px 20px
				width: 100% !important
				font-size: 18px
				outline: none