.about-nav
	ul
		padding: 0 15px
		margin: 0
		text-align: center
		background: #ececec
		overflow-x: auto
		white-space: nowrap
		li
			display: inline-block
			+ li
				margin-left: 25px
			a
				display: flex
				align-items: center
				padding: 10px 0
			&.active
				a
					border-bottom: 2px solid #e52429
