body, html {
	font-size: 13px;
	@media (min-width: 768px) {
		font-size: 14px; }
	@media (min-width: 1025px) {
		font-size: 15px; }
	@media (min-width: 1200px) {
		font-size: 16px; } }

body {
	line-height: 1;
	background: #f4f4f4; }

*,*:before,*:after {
	box-sizing: border-box; }

a {
	color: inherit;
	text-decoration: none;
	&:hover {
		text-decoration: none;
		* {
			text-decoration: none; } } }

em,i {
	color: inherit; }

table {
	border-collapse: collapse; }

input, button, textarea, select {
	font-family: 'Nunito', sans-serif; }
.uk-slidenav {
	svg {
		display: none; } }

.homepage {
	@media screen and ( min-width: 1024px ) {
		header:not(.uk-active) {
			.product-category {
				.uk-dropdown {
					display: none !important; } } } } }

.frm-btn-reset {
	display: none; }

.product-detail-page {
	.productdetailpager {
		display: none; } }

.fa-exclamation-triangle {
	color: red;
	font-size: 12px;
	font-style: italic; }
