.product-slider
	background: #fff
	box-shadow: 0px 5px 25px 0px rgba(0, 0, 0, 0.1)
	.uk-slider-items
		li
			+ li
				border-left: 1px solid #f4f4f4
	.slider-nav
		font-size: r(30px)
		color: $gray-1
		padding: 0
		&.uk-position-center-left
			left: -40px
		&.uk-position-center-right
			right: -40px
	@media screen and ( max-width: 1025px )
		.slider-nav
			&.uk-position-center-left
				left: 0
			&.uk-position-center-right
				right: 0