.sidenav
	padding: 30px
	background: #fff
	border-radius: 5px
	.title
		text-transform: uppercase
		font-size: r(20px)
		font-weight: 700
		margin-bottom: 20px
		padding-bottom: 8px
		position: relative
		&:after
			position: absolute
			content: ''
			bottom: 0
			left: 0
			width: r(60px)
			height: 2px
			background: #333
	> ul
		list-style: none
		padding-left: 0
		margin: 0
		> li
			border-top: 0
			&:hover,&.active 
				a
					color: #d8070d

			> a
				display: block
				transition: 0.3s all
				position: relative
				font-size: r(16px)
			+ li
				margin-top: 12px
				padding-top: 12px
				border-top: 1px solid #ebebeb
		