footer
	@media (max-width: 1025px)
		padding-bottom: 79px

.footer-top-wrapper
	background: #fff
	border-top: 1px solid #8e969c
	border-bottom: 1px solid #8e969c
	.uk-grid
		margin: 0 -15px
		.wrapper
			&:not(.uk-first-column)
				.item
					border-left: 1px solid #8e969c
			&.uk-grid-margin
				.item
					border-top: 1px solid #8e969c
	.item
		text-align: center
		padding: r(30px) 15px
		font-size: 14px
		display: flex
		flex-direction: column
		line-height: 20px
		.icon
			height: 64px
			margin-bottom: r(20px)
			position: relative
			img
				position: absolute
				top: 50%
				left: 50%
				transform: translate(-50%,-50%)
		.title
			font-weight: 800
			text-transform: uppercase
			margin-bottom: r(15px)
		.desc
			color: #8e969c

.footer-middle-wrapper
	padding: 20px 0
	background: $red-1
	color: white
	.subscribe-title
		display: flex
		align-items: center
		.title
			padding-left: 20px
			strong
				font-size: r(18px)
				line-height: r(22px)
				text-transform: uppercase
			p
				margin: 0
				line-height: r(24px)
	.subscribe
		padding: 0 10px
		height: 100%
		.subscribefrm
			display: flex
			height: 100%
			align-items: center
			input
				width: 495 / 705 * 100%
				height: 40px
				border-radius: 5px
				background: rgba(#000 , 0.15)
				border: 0
				padding: 0 15px
				color: white
				&::placeholder
					color: white
			button
				margin-left: 20px
				flex: 1 1 0%
				height: 40px
				border: 0
				background: #fff
				color: $red-1
				text-transform: uppercase
				font-weight: 600
				border-radius: 5px
	@media screen and ( max-width: 1025px )
		.subscribe
			padding: 0

.footer-bottom-wrapper
	background: #f8f8f8
	padding: 40px 0 90px
	.footer-title
		font-size: r(18px)
		font-weight: 700
		line-height: r(22px)
		margin-bottom: r(20px)
	.footer-info,.footer-link
		font-size: r(15px)
		color: $gray-1
		line-height: r(20px)
		ul
			list-style: none
			padding-left: 0
			margin: 0
			li
				display: flex
				+ li
					margin-top: 10px
	.footer-info
		ul
			li
				.mdi
					margin-right: 15px
					color: $gray-2
	.footer-link
		ul
			li
				.mdi
					margin-right: 8px
	.footer-social
		margin-top: 15px
		a
			+ a
				margin-left: 8px
	@media (max-width: 768px)
		padding: 40px 0

.footer-copyright-wrapper
	background: #f8f8f8
	.copyright-wrapper
		padding: 12px 0
		border-top: 1px solid #dddddd
		display: flex
		flex-wrap: wrap
		justify-content: space-between
		font-size: r(15px)
		color: #666666
		align-items: center
		margin-bottom: -10px
		> *
			margin-bottom: 10px
		.copyright
			p
				margin: 0
				text-align: center
		.payment
			span
				margin-right: 10px