.recruitment-2
	.job-list
		table
			thead
				tr
					background: $mainColor
					text-transform: uppercase
					th
						color: #fff
						font-size: r(18px)
						line-height: r(24px)
						font-weight: 600
						text-align: center
			tbody
				background: #fff
				border: 1px solid #e5e5e5
				tr
					text-align: center
					transition: 0.3s all
					+ tr
						border-top: 1px solid #e5e5e5
					td
						+ td
							border-left: 1px solid #e5e5e5
						&:nth-child(2)
							width: 50%
					&:hover
						box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.05)
						td
							color: $mainColor
	@media screen and ( max-width: 768px )
		.job-list
			table
				tbody
					tr
						td
							text-align: left
							font-size: 14px
							&:before
								content: attr(data-title) ": "
		