.cart-section 
	padding: 40px 0
	.cart-wrapper
		box-shadow: 0 5px 25px 0 rgba(0,0,0,.05)
		border-radius: 10px
		background: #fff
		padding: 30px
		.cart-title 
			margin-bottom: 20px
			font-size: 18px
			strong 
				margin-right: 5px
		.cart-table 
			margin-bottom: 10px
			border: 1px solid #e6e6e6
			background: #fff
			width: 100%
			thead 
				th:first-child 
					text-align: left
				th 
					text-align: center
					vertical-align: middle
					background: #f3f3f3
					padding: 10px 15px
					color: #333
					font-size: 15px
					font-weight: 300
					text-transform: uppercase
				th:last-child 
					text-align: right
			@media (max-width: 767px)
				thead
					border: none
					clip: rect(0 0 0 0)
					height: 1px
					margin: -1px
					overflow: hidden
					padding: 0
					position: absolute
					width: 1px
				tbody
					tr 
                        
						display: block
						margin-bottom: .625em
						padding: 20px
						td 
							border-bottom: 1px solid #ddd
							display: flex
							justify-content: flex-end
							font-size: .8em
							text-align: right!important
							width: 100%!important
							position: relative
							padding: 15px 0!important
							&:before 
								content: attr(data-label)
								font-weight: bold
								text-transform: uppercase
								position: absolute
								left: 0
								top: 50%
								transform: translateY(-50%)
						td:last-child 
							border-bottom: 0
			tbody 
				.product 
					padding: 15px
					display: flex
					text-align: left
					.product-img 
						width: 140px
						height: auto
						img 
							width: 100%
							height: 100%
							object-fit: cover
					.product-caption 
						padding-left: 30px
						.product-name 
							font-size: 16px
							font-weight: 500
							margin-bottom: 10px
							line-height: 23px
						.remove-product 
							position: relative
							white-space: nowrap
							a 
								color: #999
								font-size: 14px
								padding-left: 20px
							&:before 
								content: ""
								position: absolute
								display: block
								color: #f14b52
								font-family: Font Awesome\ 5 Free
								content: "\f057"
								top: 50%
								transform: translateY(-50%)
								left: 0
				.product-price 
					width: 120px
					font-weight: 700
				.amount 
					width: 130px
					.input-amount
						display: flex
						border-radius: 0!important
						overflow: hidden
						.btn-dec
							border-top-left-radius: 5px!important
							border-bottom-left-radius: 5px!important
						.btn-inc
							border-top-right-radius: 5px
							border-bottom-right-radius: 5px
						.btn-dec,.btn-inc
							width: 40px
							height: 40px
							display: flex
							align-items: center
							justify-content: center
							background: #d7d7d7
							font-size: 24px
						input
							height: 40px
							width: 80px
							border: 0
							text-align: center
							background: #ebebeb
				.total 
					font-weight: 700
					text-align: right
					padding: 15px
					width: 130px
				td 
					text-align: center
					font-size: 15px
		.total-amount-wrap 
			text-align: right
			padding-bottom: 20px
			.total-amount 
				font-size: 18px
				line-height: 27px
				strong 
					font-size: 20px
					color: #f90707
.cart-button 
	display: flex
	justify-content: flex-end
	padding: 20px 0
	flex-wrap: wrap
	.button-buy,.btn-back
		display: inline-flex
		align-content: center
		justify-content: center
		padding: 1rem 3rem
		border-radius: 5px
		background: #d7d7d7
		font-size: 15px
		color: #333333
		em 
			margin-right: 10px
	.button-checkout,.btn-continue
		margin-left: 20px
		display: inline-flex
		align-content: center
		justify-content: center
		padding: 1rem 3rem
		border-radius: 5px
		background: $mainColor
		font-size: 15px
		color: #fff
		em 
			margin-left: 10px
	@media (max-width: 767px)
		flex-direction: column
		.button-buy, .btn-back
			margin-bottom: 15px
			width: 100%
		.button-checkout, .btn-continue
			width: 100%
			margin-left: 0


.cart
	@media screen and ( max-width: 768px )
		.uk-dropdown
			min-width: 300px