.faq-form
	background: #fff
	padding: 20px
	.title
		margin-bottom: 20px
		color: $mainColor
		font-size: r(24px)
		font-weight: 700
	.wrap-form
		position: relative
		.form-group
			+ .form-group
				margin-top: 20px
			input[type="text"],textarea
				height: 40px
				background: #fff
				padding: 0 20px
				border: 1px solid #d7d7d7
				width: 100%
				font-size: 16px
			textarea
				height: 80px
				padding: 5px 20px
	.frm-btnwrap
		.frm-btn
			input[type="submit"]
				height: 40px
				width: 110px
				background: $mainColor
				text-transform: uppercase
				color: #ffffff
				border: 0
				letter-spacing: 0.1rem
				font-size: 16px
				cursor: pointer
		.message 
			padding-top: 15px
			font-size: 14px 
			line-height: 20px
			font-style: italic